import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Check } from "lucide-react";

export default function SignUp0() {
  const navigate = useNavigate();

  //   useState 
  const [user_id, setUser_id] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPw, setConfirmPw] = useState("");
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");

  //  ""   
  const handleNext = (e) => {
    e.preventDefault();

    // ()      
    if (password !== confirmPw) {
      alert("  .");
      return;
    }

    //    ,   
    navigate("/signup1", {
      state: {
        user_id,
        password,
        username,
        email,
        phone,
      },
    });
  };

  return (
    <div className="min-h-screen bg-white">
      <main className="container mx-auto px-4 max-w-md py-10">
        <form onSubmit={handleNext}>
          {/* ID Section */}
          <div className="mb-8">
            <label className="block text-gray-700 mb-2 text-lg"></label>
            <div className="relative">
              <input
                type="text"
                placeholder="ID "
                value={user_id}
                onChange={(e) => setUser_id(e.target.value)}
                className="w-full bg-[#f4f4f4] rounded-md py-3 px-4 text-gray-700 focus:outline-none"
              />
              <div className="absolute right-3 top-1/2 transform -translate-y-1/2 bg-[#4ba6f7] rounded-full p-1">
                <Check className="h-4 w-4 text-white" />
              </div>
            </div>
          </div>

          {/* Password Section */}
          <div className="mb-8">
            <label className="block text-gray-700 mb-2 text-lg"></label>
            <div className="relative mb-3">
              <input
                type="password"
                placeholder=" "
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="w-full bg-[#f4f4f4] rounded-md py-3 px-4 text-gray-700 focus:outline-none"
              />
              <div className="absolute right-3 top-1/2 transform -translate-y-1/2 bg-[#4ba6f7] rounded-full p-1">
                <Check className="h-4 w-4 text-white" />
              </div>
            </div>
            <div className="relative mb-2">
              <input
                type="password"
                placeholder=" "
                value={confirmPw}
                onChange={(e) => setConfirmPw(e.target.value)}
                className="w-full bg-[#f4f4f4] rounded-md py-3 px-4 text-gray-700 focus:outline-none"
              />
              <div className="absolute right-3 top-1/2 transform -translate-y-1/2 bg-[#4ba6f7] rounded-full p-1">
                <Check className="h-4 w-4 text-white" />
              </div>
            </div>
            <p className="text-[#8a8a8a] text-xs flex items-center">
              <span className="inline-block mr-1"></span>  , , 
               8  .
            </p>
          </div>

          {/* Username Section */}
          <div className="mb-8">
            <label className="block text-gray-700 mb-2 text-lg"> </label>
            <input
              type="text"
              placeholder=" "
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              className="w-full bg-[#f4f4f4] rounded-md py-3 px-4 text-gray-700 focus:outline-none"
            />
          </div>

          {/* Email Section */}
          <div className="mb-8">
            <label className="block text-gray-700 mb-2 text-lg"> </label>
            <input
              type="email"
              placeholder=": example@gmail.com"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="w-full bg-[#f4f4f4] rounded-md py-3 px-4 text-gray-700 focus:outline-none"
            />
          </div>

          {/* Phone Section */}
          <div className="mb-8">
            <label className="block text-gray-700 mb-2 text-lg"> </label>
            <input
              type="tel"
              placeholder=":010-0000-0000"
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
              className="w-full bg-[#f4f4f4] rounded-md py-3 px-4 text-gray-700 focus:outline-none mb-2"
            />
            <p className="text-[#8a8a8a] text-xs">
                 ,    ,  .
            </p>
          </div>

          {/* Submit Button */}
          <button
            type="submit"
            className="w-full bg-[#4ba6f7] text-white py-4 rounded-md font-medium text-lg hover:bg-[#3a95e6] transition-colors"
          >
            
          </button>
        </form>
      </main>
    </div>
  );
}
