import React from "react";
import { Link } from "react-router-dom";
import useAuthStore from "../../components/context/authStore.js";
// import Logo from '../../assets/images/logo.svg';

export default function Header() {
  const { user, logout } = useAuthStore();
 
  return (
    <header className="border-b border-[#bbbbbb]">
      <div className="max-w-7xl mx-auto px-4 py-4 flex justify-between items-center">
        <Link to="/" className="text-[#4ba6f7] text-3xl font-bold">
          Fundit
        </Link>
        <nav className="space-x-6 text-[#8a8a8a]">
          {user ? (
            //  
            <>
              <span>{user.username}, !</span>
              <Link to="/mypage" className="hover:text-[#4ba6f7]"></Link>
              <button onClick={logout} className="hover:text-[#4ba6f7]"></button>
              <Link to="/services" className="hover:text-[#4ba6f7]"></Link>
            </>
          ) : (
            //  
            <>
             <Link to="/login" className="hover:text-[#4ba6f7]"> </Link>
             <Link to="/signup" className="hover:text-[#4ba6f7]"> </Link>
            </>
          )}
        </nav>
      </div>
    </header>
  );
}
